import { Card, CardContent, CardMedia, CardActions, Rating, Typography, Box, Chip, Button } from '@mui/material'
import React from 'react'
import useStyles from "./styles"
import LocationOnIcon from '@mui/icons-material/LocationOn';
import PhoneIcon from '@mui/icons-material/Phone';

const PlaceDetails = ({ place, selected, refProp }) => {
  const { classes } = useStyles()

  if (selected) refProp?.current?.scrollIntoView({ behavior: 'smooth', block: 'start' });
  
  return (
    <Card elevation={6} style={{ backgroundColor: "#333333d8", marginTop:"-15px" }}>
      <CardMedia 
        style={{ height: "160px" }}
        image={place.photo ? place.photo.images.large.url : 'https://www.foodserviceandhospitality.com/wp-content/uploads/2016/09/Restaurant-Placeholder-001.jpg'}
        title={place.name}
      />
      <CardContent>
        <Typography className={classes.typ} style={{fontSize: "18px"}} gutterBottom variant="h5">{place.name}</Typography>
        <Box display={"flex"} justifyContent="space-between" my={2}>
          <Rating name={"read-only"} value={Number(place.rating)} readOnly />
          <Typography className={classes.typ} component="legend">{place.num_reviews} review{place.num_reviews > 1 && 's'}</Typography>
        </Box>
        <Box display="flex" justifyContent="space-between">
          <Typography className={classes.typ} component="legend">Ranking</Typography>
          <Typography className={classes.typ} gutterBottom variant="subtitle1">
            {place.ranking}
          </Typography>
        </Box>
        <Box display={"flex"} justifyContent="space-between">
          <Typography className={classes.typ} component={"legend"}>Price</Typography>
          <Typography className={classes.typ} gutterBottom variant="subtitle1">{place.price_level}</Typography>
        </Box>
        {place?.awards?.map((award) => (
          <Box display="flex" justifyContent="space-between" my={1} alignItems="center">
            <img src={award.images.small} alt={place.name} />
            <Typography variant="subtitle2" className={classes.typ}>{award.display_name}</Typography>
          </Box>
        ))}
        {place?.cuisine?.map(({ name }) => (
          <Chip key={name} size="small" label={name} className={classes.chip} />
        ))}
        {place.address && (
          <Typography gutterBottom variant="subtitle12" color="#f1f1f1" className={classes.subtitle}>
            <LocationOnIcon />{place.address}
          </Typography>
        )}
        {place.phone && (
          <Typography variant="subtitle2" className={classes.spacing}>
            <PhoneIcon /> {place.phone}
          </Typography>
        )}
      </CardContent>
      <CardActions>
        <Button size="small" color="primary" onClick={() => window.open(place.web_url, '_blank')}>
          Trip Advisor
        </Button>
        <Button size="small" color="primary" onClick={() => window.open(place.website, '_blank')}>
          Website
        </Button>
      </CardActions>
    </Card>
  )
}

export default PlaceDetails