import axios from 'axios'

const URL = 'https://travel-advisor.p.rapidapi.com/restaurants/list-in-boundary' // {Rapid Api} + name of GET api => restaurants/list-in-boundary (Deprecated)

export const getPlacesData = async (type, sw, ne) => {
  try {
    const { data: { data } } = await axios.get(`https://travel-advisor.p.rapidapi.com/${type}/list-in-boundary`, {
      params: {
        bl_latitude: sw.lat,
        tr_latitude: ne.lat,
        bl_longitude: sw.lng,
        tr_longitude: ne.lng,
      },
      headers: {
        'X-RapidAPI-Key': 'e064abe485msh8d23185f69a9315p1c3de3jsnfc1616f5ab58',
        'X-RapidAPI-Host': 'travel-advisor.p.rapidapi.com'
      }
    })
    return data
  } catch (err) {
    console.log(err)
  }
}

export const getPlacesWeather = async () => {
  try {
    const { data } = await axios.get('https://open-weather13.p.rapidapi.com/city/landon')
  } catch (error) {
    console.log(error)
  }
}