import { Paper, useMediaQuery, Typography, Rating } from '@mui/material'
import GoogleMapReact from 'google-map-react';
import React from 'react'
import LocationOnIcon from '@mui/icons-material/LocationOn';
import useStyles from './styles'

const Map = ({ places, setCoord, coord, setBounds, setCheckedChild }) => {
  const { classes } = useStyles()
  const isMobile = useMediaQuery('(min-width: 600px)')

  return (
    <div className={classes.mapContainer}>
      <GoogleMapReact
        bootstrapURLKeys={{ key: 'AIzaSyBwWuC3c-i_QLHhQoVfCHy7fbVRdCHoOIY' }}
        defaultCenter={coord}
        center={coord}
        defaultZoom={14}
        options={''}
        onChange={(e) => {
          setCoord({ lat: e.center.lat, lng: e.center.lng })
          setBounds({ ne: e.marginBounds.ne, sw: e.marginBounds.sw })
        }}
        onChildClick={(child) => setCheckedChild(child)}
      >
        {places?.map((place, i) => (
          <div 
            key={i} 
            className={classes.markerContainer}
            lat={Number(place.latitude)}
            lng={Number(place.longitude)}
          >
            {!isMobile ? <LocationOnIcon /> : (
              <Paper elevation={3} className={classes.paper}>
                <Typography className={classes.typography} variant="subtitle2" gutterBottom> {place.name}</Typography>
                <img src={place.photo ? place.photo.images.large.url : 'https://www.foodserviceandhospitality.com/wp-content/uploads/2016/09/Restaurant-Placeholder-001.jpg'} alt={place.name} />
                <Rating name="read-only" size="small" value={Number(place.rating)} readOnly />
              </Paper>
            )}
          </div>
        ))}
      </GoogleMapReact>
    </div>
  )
}

export default Map