import CssBaseline from '@mui/material/CssBaseline'
import Header from './components/Header/Header';
import Grid from '@mui/material/Grid';
import List from './components/List/List';
import Map from './components/Map/Map';
import "./"
import { useEffect, useState } from 'react';
import { getPlacesData } from './api';


function App() {
  const [places, setPlaces] = useState([])
  const [filteredPlaces, setFilteredPlaces] = useState([])

  const [checkedChild, setCheckedChild] = useState(null)

  const [coord, setCoord] = useState({})
  const [bounds, setBounds] = useState({ sw: 0, ne: 0 })
  const [isLoading, setIsLoading] = useState(false)

  const [type, setType] = useState('restaurants')
  const [rate, setRate] = useState(0)

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(({ coords: { latitude, longitude } }) => {
      setCoord({ lat: latitude, lng: longitude })
    })
  }, [])

  useEffect(() => {
    const ratingDep = places.filter(place => Number(place.rating) >= Number(rate))
    setFilteredPlaces(ratingDep)
  }, [rate, places])

  useEffect(() => {
    if (bounds) {
      setIsLoading(true)
      getPlacesData(type, bounds.sw, bounds.ne).then((data) => {
        setPlaces(data.filter(place => place.name && place.num_reviews))
        setIsLoading(false)
      })
    }
  }, [type, coord, bounds])

  return (
    <>
      <CssBaseline />
      <Header setCoords={setCoord} />
      <Grid container spacing={3} style={{ width: '100%' }} >
        <Grid item xs={12} md={4} >
          <List
            places={filteredPlaces.length ? filteredPlaces : places}
            checkedChild={checkedChild}
            isLoading={isLoading}
            type={type}
            setType={setType}
            rate={rate}
            setRate={setRate}
          />
        </Grid>
        <Grid item xs={12} md={8} >
          <Map
            coord={coord}
            setCoord={setCoord}
            setBounds={setBounds}
            places={places}
            setCheckedChild={setCheckedChild}
          />
        </Grid>
      </Grid>
    </>
  );
}

export default App;
